.card {
  margin: 1rem;
  position: relative;
  width: 30%;
  text-align: left;
  font-family: "Raleway", sans-serif;
}

img {
  height: 100%;
  width: 100%;
}

.card > .content > ul {
  list-style-type: none;
}

.wrapper {
  padding-top: 50px;
  background: #eceff1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: Purple;
  margin-bottom: 50px;
  font-family: "Raleway", sans-serif;
}

